  --shadow-secondary: hsl(var(--shadow-secondary-hue), 90%, 60%);
  --border: 4px;
  --shimmy-distance: 5;
  --clip-one: polygon(
		0 2%,
		100% 2%,
		100% 95%,
		95% 95%,
		95% 90%,
		85% 90%,
		85% 95%,
		8% 95%,
		0 70%
	);
  --clip-two: polygon(
		0 78%,
		100% 78%,
		100% 100%,
		95% 100%,
		95% 90%,
		85% 90%,
		85% 100%,
		8% 100%,
		0 78%
	);
  --clip-three: polygon(
		0 44%,
		100% 44%,
		100% 54%,
		95% 54%,
		95% 54%,
		85% 54%,
		85% 54%,
		8% 54%,
		0 54%
	);
  --clip-four: polygon(
		0 0,
		100% 0,
		100% 0,
		95% 0,
		95% 0,
		85% 0,
		85% 0,
		8% 0,
		0 0
	);
  --clip-five: polygon(
		0 0,
		100% 0,
		100% 0,
		95% 0,
		95% 0,
		85% 0,
		85% 0,
		8% 0,
		0 0
	);
  --clip-six: polygon(
		0 40%,
		100% 40%,
		100% 85%,
		95% 85%,
		95% 85%,
		85% 85%,
		85% 85%,
		8% 85%,
		0 70%
	);
  --clip-seven: polygon(
		0 63%,
		100% 63%,
		100% 80%,
		95% 80%,
		95% 80%,
		85% 80%,
		85% 80%,
		8% 80%,
		0 70%
	);
  font-family: "Cyber","Rock Salt", cursive;
  color: var(--color);
  cursor: pointer;
  background: transparent;
  text-transform: uppercase;
  font-size: var(--font-size);
  outline: transparent;
  letter-spacing: 2px;
  position: relative;
  font-weight: 700;
